import React, { useRef, useEffect } from "react";
import logo from "./logo.svg";
import "./App.css";

import jeffoufe from "./jeffoufe.gif";
import headphones from "./headphones.png";
import stardust from "./stardust.mp3";
import bucket from "./bucket.mp4";
import mix from "./mix.png";
import what from "./what.png";

const backgroundColors = [
  "#ff92b7",
  "#8af311",
  "aqua",
  "blanchedalmond",
  "cornflowerblue",
  "deeppink"
];
function useInterval(callback, delay) {
  const savedCallback = useRef();
  // Remember the latest callback.
  useEffect(() => {
    savedCallback.current = callback;
  }, [callback]);

  // Set up the interval.
  useEffect(() => {
    function tick() {
      savedCallback.current();
    }
    if (delay !== null) {
      let id = setInterval(tick, delay);
      return () => clearInterval(id);
    }
  }, [delay]);
}

function App() {
  const [backgroundColorIndex, setBackgroundColorIndex] = React.useState(0);
  const [video, setVideo] = React.useState(false);
  useInterval(() => {
    setBackgroundColorIndex(
      (backgroundColorIndex + 1) % backgroundColors.length
    );
  }, 500);
  return (
    <div
      className="App"
      style={{
        backgroundImage: `url("${jeffoufe}")`,
        backgroundColor: backgroundColors[backgroundColorIndex],
        backgroundSize: "20vh",
        cursor: `url('${headphones}'), auto`
      }}
    >
      <header className="App-header">
        <h1 className="title">Jeffoufe.com</h1>
      </header>

      <div className="mediaHolder">
        {!video && (
          <div
            className="mixHolder"
            style={{ backgroundImage: `url("${mix}")` }}
            onClick={() => {
              setVideo(true);
            }}
          >
            <p>Clicke pour un mix de Qualité</p>
          </div>
        )}
        {video && (
          <div className="videoHolder">
            <video src={bucket} autoPlay loop muted className="video" />
            <div className="videoText">
              <img src={what} className="App-logo" alt="the master thinking" />
              <p className="blinkMe">T'AS PAS CHANGÉ JEFF !!</p>
              <img src={what} className="App-logo" alt="the master thinking" />
            </div>
            <audio src={stardust} autoPlay loop className="audio" />
          </div>
        )}
      </div>
      <div>
        <a
          className="App-link"
          href="http://jeffoufe.com"
          target="_blank"
          rel="noopener noreferrer"
        >
          Jeffoufe.com
        </a>
      </div>
    </div>
  );
}

export default App;
